import './App.css';
import '@mantine/core/styles.css';
import { Table, MantineProvider, SimpleGrid, Select, Container, Group } from '@mantine/core';
import { useEffect, useState } from 'react';
import axios from 'axios';
import classes from './HeaderTab.module.css'


function App() {

  if (useState === undefined) {
    throw new Error('React.useState is not defined. Please ensure you are using a compatible version of React.');
  }

  const [lessons, setLessons] = useState([]);

  useEffect(() => {
    fetchLessons();
  }, []);

  const fetchLessons = async () => {
    try {
      const response = await axios.get('/api/v1/lessons');
      setLessons(await response.data);
    } catch (error) {
      console.error('Error fetching lessons:', error);
    }
  };


  const handleRowClick = (lesson) => {
    console.log('Row clicked:', lesson);
    // Implement any additional logic for row click if needed
  };

  const renderTableRows = () => {
    return lessons.map((lesson) => (
      <Table.Tr key={lesson.id} onClick={() => handleRowClick.bind(this, lesson)}>
        <Table.Td>{lesson.time}</Table.Td>
        <Table.Td>{lesson.track.name}</Table.Td>
        <Table.Td>{lesson.bell}</Table.Td>
        <Table.Td>{lesson.library.name}</Table.Td>
      </Table.Tr>
    ));
  };

  const renderItems = () => {
    return lessons.map((lesson) => (
      <li
        key={lesson.id}
        className="list-group-item d-flex justify-content-between align-items-center"
      >
        <span 
          title={lesson.name}
        >
          {lesson.bell} - {lesson.track.name} - {lesson.library.name}
        </span>
        <span>
          <button
            className="btn btn-secondary mr-2"
          >
            Edit
          </button>
          <button
            className="btn btn-danger"
          >
            Delete
          </button>
        </span>
      </li>
    ));
  };

  return (
    <div className="App">
      <MantineProvider>

        <div className={classes.header}>
          <Container size="md">
            <Group align="right" position="apart">
              <Select allowDeselect mt="sm" data={['SK', 'CZ', 'HU', 'PL']} />
            </Group>
          </Container>
        </div>

        <div className={classes.content}>
          <Container size="md" style={{ marginTop: '5px' }}>
            <SimpleGrid cols={{ base: 1, sm: 1 }} spacing="md">
              <Table>
                <Table.Thead>
                  <Table.Tr>
                    <Table.Th>Čas</Table.Th>
                    <Table.Th>Zvuková stopa</Table.Th>
                    <Table.Th>Zvonček</Table.Th>
                    <Table.Th>Knižnica</Table.Th>
                  </Table.Tr>
                </Table.Thead>
                <Table.Tbody>
                  {renderTableRows()}
                </Table.Tbody>
              </Table>
            </SimpleGrid>
          </Container>
        </div>
        <div className="row ">
          <div className="col-md-6 col-sm-10 mx-auto p-0">
            <div className="card p-3">
              <div className="">
                {renderItems()}
              </div>
            </div>
          </div>
        </div>
      </MantineProvider>
    </div>
  );

};

export default App;


